import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { Router, Route, Switch } from "react-router-dom";

import "assets/scss/material-kit-react.scss?v=1.9.0";

// pages for this product
import Components from "views/Components/Components.js";
import MaterialKit from "views/Components/MaterialKit.js";
import LandingPage from "views/LandingPage/LandingPage.js";
import ProfilePage from "views/ProfilePage/ProfilePage.js";
import LoginPage from "views/LoginPage/LoginPage.js";
import MindSection from "views/LandingPage/Sections/MindSection.js";
import BodySection from "views/LandingPage/Sections/BodySection.js";
import SpiritSection from "views/LandingPage/Sections/SpiritSection.js";
import ForumSection from "views/LandingPage/Sections/ForumSection.js";

var hist = createBrowserHistory();

ReactDOM.render(
  <Router history={hist}>
    <Switch>
      // I am commenting this out because I don't want the original Material Kit Live version to be completly gone.
      // <Route path="/landing-page" component={LandingPage} />
      // <Route path="/profile-page" component={ProfilePage} />
      // <Route path="/login-page" component={LoginPage} />
      <Route path="/mind-section" component={MindSection} />
      <Route path="/body-section" component={BodySection} />
      <Route path="/spirit-section" component={SpiritSection} />
      <Route path="/forum-section" component={ForumSection} />
      <Route path="/material-kit" component={MaterialKit} />
      <Route path="/" component={LandingPage} />
    </Switch>
  </Router>,
  document.getElementById("root")
);
