import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
import Camera from "@material-ui/icons/Camera";
import Palette from "@material-ui/icons/Palette";
import Favorite from "@material-ui/icons/Favorite";
import ImportContacts from "@material-ui/icons/ImportContacts";
import Theaters from "@material-ui/icons/Theaters";
import MusicNote from "@material-ui/icons/MusicNote";
// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import Button from "components/CustomButtons/Button.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import NavPills from "components/NavPills/NavPills.js";
import Parallax from "components/Parallax/Parallax.js";

import profile from "assets/img/faces/christian.jpg";

import studio1 from "assets/img/examples/studio-1.jpg";
import studio2 from "assets/img/examples/studio-2.jpg";
import studio3 from "assets/img/examples/studio-3.jpg";
import studio4 from "assets/img/examples/studio-4.jpg";
import studio5 from "assets/img/examples/studio-5.jpg";
import grain from "assets/img/examples/the-grain-brain-cover.jpg";
import recall from "assets/img/examples/total-recall-cover.jpg";
import habit from "assets/img/examples/the-power-of-habit-cover.png";
import work1 from "assets/img/examples/olu-eletu.jpg";
import work2 from "assets/img/examples/clem-onojeghuo.jpg";
import work3 from "assets/img/examples/cynthia-del-rio.jpg";
import work4 from "assets/img/examples/mariya-georgieva.jpg";
import work5 from "assets/img/examples/clem-onojegaw.jpg";

import styles from "assets/jss/material-kit-react/views/profilePage.js";

const useStyles = makeStyles(styles);

export default function ProfilePage(props) {
  const classes = useStyles();
  const [checked, setChecked] = React.useState([24, 22]);
  const [checkedA, setCheckedA] = React.useState(false);
  const { ...rest } = props;
  const imageClasses = classNames(
    classes.imgRaised,
    classes.imgRoundedCircle,
    classes.imgFluid
  );
  const navImageClasses = classNames(classes.imgRounded, classes.imgGallery);
  return (
    <div>
      <Header
        color="transparent"
        brand="Mind Over Matter"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 200,
          color: "white"
        }}
        {...rest}
      />
      <Parallax small filter image={require("assets/img/bg3.jpg")}>
        <div className={classes.parallaxContainer}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={6}>
              <h1 className={classes.parallaxTitle}>Body</h1>
            </GridItem>
          </GridContainer>
        </div>
      </Parallax>
      <div className={classNames(classes.main, classes.mainRaised)}>
        <div>
          <div className={classes.container}>
            <GridContainer justify="center">
              <GridItem xs={12} sm={12} md={8}>
                <div className={classes.profile}>
                  <h3 className={classes.title}>"Let the Spirit be the anvil that remains rooted to the earth, the mind be the hammer that remains firm to 
                  the handle and the body be the sword that is shaped by both the Mind and Spirit."</h3>
                </div>
              </GridItem>
            </GridContainer>
            <div className={classes.description}>
              <p>
                I really believe that your body is a temple and you are what you eat. In order to maintain a healthy body, you must get to know your own body
                and educate yourself on healthy eating. Read the labels on the food facts, take in as much information of what you intake even on your skin care
                routine. It always makes me sad when I see people eating less because they want to eat more, or eating super unhealthy because they want to gain
                weight, but there must be a balance in order to maintain a healthy and fit body. I believe that you should eat more and not less, that being said 
                one must do the necessary exercise in order to reach the goal to obtain the desired body. I remember when I started working out, I first learned
                about protein and what it does to your body so I started taking protein eberytime after my workout. Then I leared about multivitamins, so I used
                that for my citamins and minerals. I then wanted to become stronger and lift heavier at the gym so I learned about pre-workout to give me that 
                extra boost but I learned about the ingredients so I would limit myself from taking it often.  What I do is I alternate, I take it for two weeks 
                and then I take a break for two weeks. Over time I learned more about different supplements, and started to eat more proteins, carbohydrates and 
                alot of green. It was through fitness that I started reading the food facts, that I started to feel more confident about my body, my energy level
                went through the roof so I was able to use that extra energy towards school, and I started dressing better as well. Aside from what you intake, 
                you also need that mind, body and spirit connection to the development of a healthy body. I've noticed that when you focus on the internal it all
                spills over to the external. That is why the above quote I decided to use the metaphore of the anvil, the hammer and the sword. To make a sharp
                sword, you'd need a solid anvil and hammer and you yourself are the blacksmith.
              </p>
            </div>
            <GridContainer justify="center">
              <GridItem xs={12} sm={12} md={8} className={classes.navWrapper}>
                <NavPills
                  alignCenter
                  color="primary"
                  tabs={[
                    {
                      tabButton: "Books",
                      tabIcon: ImportContacts,
                      tabContent: (
                        <GridContainer justify="center">
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={grain}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={recall}
                              className={navImageClasses}
                            />
                          </GridItem>
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={habit}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={studio4}
                              className={navImageClasses}
                            />
                          </GridItem>
                        </GridContainer>
                      )
                    },
                    {
                      tabButton: "Movies",
                      tabIcon: Theaters,
                      tabContent: (
                        <GridContainer justify="center">
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={work1}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={work2}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={work3}
                              className={navImageClasses}
                            />
                          </GridItem>
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={work4}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={work5}
                              className={navImageClasses}
                            />
                          </GridItem>
                        </GridContainer>
                      )
                    },
                    {
                      tabButton: "Music",
                      tabIcon: MusicNote,
                      tabContent: (
                        <GridContainer justify="center">
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={work4}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={studio3}
                              className={navImageClasses}
                            />
                          </GridItem>
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={work2}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={work1}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={studio1}
                              className={navImageClasses}
                            />
                          </GridItem>
                        </GridContainer>
                      )
                    }
                  ]}
                />
              </GridItem>
            </GridContainer>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}
